<template>
<div>
    <section class="content-header">
      <h1>Users</h1>
    </section>
    <section class="content vld-parent" ref="ref_load_filter_user">
      <div class="card box-warning">
        <div class="box-header with-border">
          <h3 class="box-title card-text">User Search</h3>
        </div>
        <div class="box-body">
          <form @submit.prevent="loadUsers()" class="row">
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Name</label>
                <input type="text" class="form-control" placeholder="Enter Name ..." v-model="search.name">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Phone No.</label>
                <input type="tel" class="form-control" placeholder="Enter Phone No. ..." v-model="search.phone">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Email</label>
                <input type="email" class="form-control" placeholder="Enter Email ..." v-model="search.email">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Organisation</label>
                <select class="form-control" v-model="search.organisation">
                <option value="">Select Organisation</option>
                  <option value="United Scrap Metal">United Scrap Metal</option>
                  <option value="Others">Others</option>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Role</label>
                <select class="form-control" v-model="search.type">
                    <template v-if="search.organisation=='United Scrap Metal'">
                        <option value="Admin">Admin</option>
                        <option value="Finance Team">Finance Team</option>
                        <option value="Transportation Team"> Transportation Team</option>
                        <option value="Sales Team ">Sales Team </option>
                        <option value="CEC">CEC </option>
                        </template>
                        <template v-else>
                        <option value="Primary">Primary</option>
                        <option value="Finance">Finance</option>
                        <option value="Transportation">Transportation </option>
                      </template>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label style="display: block;"> &nbsp;&nbsp; </label>
                <button type="submit" class="btn link1">Filter</button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div class="card box-black">
        <div class="box-header with-border">
          <h3 class="box-title card-text">Users Table</h3>
        </div>
        <div class="bottom-box-header">
          <div class="pull-left">
            <div class="Showingbox"> <span class="pull-left">Showing </span>
              <div class="pull-left drop">
                <select class="form-control" v-model="ppp">
                  <option value="25">25</option>
                  <option value="50">50</option>
                  <option value="100">100</option>
                </select>
              </div>
              <span class="pull-left">entries </span>
              <div class="clearfix"></div>
            </div>
          </div>
          <div class="pull-right"> <router-link to="/user/add" class="btn link1"> <span><i class="fa fa-plus-square-o" aria-hidden="true"></i> </span> Add User </router-link></div>
          <div class="clearfix"></div>
        </div>
        <div class="box-body">
          <div class="tablesection">
            <div class="table-responsive">
              <table class="table table-striped table-hover">
                <thead>
                  <tr>
                    <th style="text-align: center;" width="70">Sl No.</th>
                    <th>Name </th>
                    <th>Phone No.</th>
                    <th>Email</th>
                    <th>Organisation.</th>
                    <th>Role</th>
                    <th>Status </th>
                  </tr>
                </thead>


                <tbody v-if="ulist && ulist.data">
                  <tr v-for="(ua,x) in ulist.data" :key="ua.id" >
                    <td align="center">{{x+1}}</td>
                    <td><router-link :to="'/user/edit/'+ua.id" class="text-green">{{ua.name}}</router-link></td>
                    <td>{{ua.phone}}</td>
                    <td>{{ua.email}}</td>
                    <td>{{ua.organization}}</td>
                    <td>{{ua.type}}</td>
                    <td><span class="badge" :class="ua.status=='Active'||ua.status=='Approve'?'bg-green':'bg-red'">{{ua.status}}</span></td>
                  </tr>

                </tbody>
              </table>
            </div>
          </div>
          <div class="paginationsectionbox">
            <div class="row" v-if="ulist && ulist.data">
              <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                <div class="Showingboxto1">Showing {{ ulist.from }} to {{ ulist.to }} of {{ ulist.total }} results</div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-7 col-lg-7">
                <div class="Showingboxto2">
                  <ul>
                   <li v-for="(links, index) in ulist.links" :key="index" :class="{'active disabled':links.active, 'disabled':!links.url, 'paginate_button':index!= 0 && index+1!=ulist.links.length }">
                        <a href="javascript:void(0);" v-html="links.label" @click="loadUsers(links.url);"></a>
                    </li>
                  </ul>


                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>


  </div>
</template>

<script>
    export default {
      name:"userlist",
        data () {
            return {
                ppp:25,
                search:{
                  organisation:'',
                  type:'',
                  name:'',
                  phone:'',
                  email:''
                },
                editmode: false,
                ulist:"",
                users : {},
                form: new Form({
                    id : '',
                    type : '',
                    name: '',
                    email: '',
                    password: '',
                    email_verified_at: '',
                })
            }
        },
        watch: {
          async ppp(){
            await this.loadUsers()
          },
                              async 'search.organization'(n){
        if(n=='United Scrap Metal'){
          if(!['Admin','Finance Team','Transportation Team','Sales Team','CEC'].includes(this.search.type)){
            this.search.type=''
          }
      }else{
         if(!['Primary','Finance','Transportation'].includes(this.search.type)){
            this.search.type=''
          }
      }
      }
        },
        methods: {

            deleteUser(id){
                Swal.fire({
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    showCancelButton: true,
                    confirmButtonColor: '#d33',
                    cancelButtonColor: '#3085d6',
                    confirmButtonText: 'Yes, delete it!'
                    }).then((result) => {

                        // Send request to the server
                         if (result.value) {
                                this.form.delete('api/user/'+id).then(()=>{
                                        Swal.fire(
                                        'Deleted!',
                                        'Your file has been deleted.',
                                        'success'
                                        );
                                    // Fire.$emit('AfterCreate');
                                    this.loadUsers();
                                }).catch((data)=> {
                                  Swal.fire("Failed!", data.message, "warning");
                              });
                         }
                    })
            },
          loadUsers(url='/api/user'){
           // if(this.$gate.isAdmin()){
             // axios.get("api/user").then(({ data }) => (this.users = data));
                let UFloder = this.$loading.show({
                    container: this.$refs.ref_load_filter_user
                });
            axios.get(url,{
              params: {
                  organisation:this.search.organisation,
                  type:this.search.type,
                  name:this.search.name,
                  phone:this.search.phone,
                  email:this.search.email,
                  ppp: this.ppp,
	            }
            })
                  .then((res) => {
                      this.ulist = res.data.data
                       UFloder.hide();
                  })
                  .catch(err => {
                    Toast.fire({
                      icon: 'error',
                      title: 'Some error occured! Please try again'
                  });
                      UFloder.hide();
                  });
          },



        },
        mounted() {
            console.log('User Component mounted.')
        },
        created() {

            this.$Progress.start();
            this.loadUsers();
            this.$Progress.finish();
        }
    }
</script>
